import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
	state:{
		gdzc:""
	},
	mutations:{
		sets(state,data){
			state.gdzc = data
		}
	},
	getters:{
		get_Name:state=>state.name+'getters'
	},
	actions:{
		action({commit}){
//			异步操作但实际上还是提交mutations改变值
			commit('sets',"yuchunhui")
		}
	}
})



